

























import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class WebtoonList extends Vue {
  private winY = window.innerHeight;
  private winX = window.innerWidth;
  private redt = `translate(30vw,-${this.winY * 0.32}px)`;
  private bh1t = "translateX(-200vw)";
  private bv1t = `translateY(${this.winY * 2}px)`;
  private bluet = `translateY(${this.winY * 0.2}px)`;
  private bh2t = "translateX(-200vw)";
  private bv2t = "translateX(100vw)";
  private bh3t = `translateY(-${this.winY * 3}px)`;
  private yellowt = "translateX(-8vw)";
  private scrollY = 0;
  private scrollTimer = null;
  private progressnow = 0;

  private mounted() {
    this.$app.$el.addEventListener("resize", this.resizing);
    this.$app.$el.addEventListener("scroll", this.scrolling);
    // (this.$el as HTMLElement).style.height = `${2 * this.winY}px`;
  }
  private beforeDestroy() {
    this.$app.$el.removeEventListener("resize", this.resizing);
    this.$app.$el.removeEventListener("scroll", this.scrolling);
  }

  private resizing() {
    this.winY = window.innerHeight;
    this.winX = window.innerWidth;
  }
  private scrolling() {
    this.scrollY = this.$app.$el.scrollTop;
    this.progressnow = this.scrollY / this.winY + 0.01;
    requestAnimationFrame(this.ani);
  }

  private ani() {
    let p = Math.min(this.progressnow, 1);
    this.redt = `translate(${30 - p * 30}vw,-${
      this.winY * 0.32 - p * this.winY * 0.32
    }px)`;
    this.bh1t = `translateX(-${200 - p * 200}vw)`;
    this.bv1t = `translateY(${this.winY * 2 - p * this.winY * 2}px)`;
    this.bluet = `translateY(${this.winY * 0.2 - p * this.winY * 0.2}px)`;
    this.bh2t = `translateX(-${200 - p * 200}vw)`;
    this.bv2t = `translateX(${100 - p * 100}vw)`;
    this.bh3t = `translateY(-${this.winY * 3 - p * this.winY * 3}px)`;
    this.yellowt = `translateX(-${8 - p * 8}vw)`;
    // requestAnimationFrame(this.ani);
  }
}
