
















import { Component, Vue } from "vue-property-decorator";

interface IMapObj {
  type: string;
  contentF: string;
  contentB: string;
  element: HTMLElement;
  innerElements: Array<HTMLImageElement | HTMLIFrameElement>;
  state: {
    x: number;
    y: number;
    thetaX: number;
    thetaZ: number;
    width: number;
    height: number;
  };
}

@Component({
  components: {},
})
export default class List extends Vue {
  private init = false;
  private map = {
    xMax: 30,
    yMax: 30,
  };
  private state = {
    x: 1,
    y: 1,
    thetaX: 1.7,
    thetaZ: 0,
  };
  private keyIn = {
    Au: false,
    Ad: false,
    Al: false,
    Ar: false,
    Du: false,
    Dd: false,
    Dl: false,
    Dr: false,
  };
  private moving = false;
  private minimapSize = 0;
  private screenSize = [0, 0];
  private screenRatio = 0;
  private objects: IMapObj[] = [
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/dog.png",
      contentB: "https://dororodoroddo.github.io/img/back.jpg",
      state: {
        x: 20,
        y: 10,
        thetaX: 10,
        thetaZ: 0,
        width: 10,
        height: 10,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/cat_transparent.png",
      contentB: "",
      state: {
        x: 13,
        y: 3,
        thetaX: 10,
        thetaZ: 90,
        width: 2,
        height: 2,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/dot_transparent.png",
      contentB: "",
      state: {
        x: 10,
        y: 20,
        thetaX: 10,
        thetaZ: 0,
        width: 8,
        height: 8,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/tutorial1.png",
      contentB: "https://dororodoroddo.github.io/img/back.jpg",
      state: {
        x: 0,
        y: 10,
        thetaX: 0,
        thetaZ: 90,
        width: 8,
        height: 8,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/tutorial2.png",
      contentB: "https://dororodoroddo.github.io/img/back.jpg",
      state: {
        x: 5,
        y: 5,
        thetaX: 0,
        thetaZ: 45,
        width: 8,
        height: 8,
      },
    },
  ];

  private mounted() {
    this.minimapSize = (this.$el.querySelector("#minimap") as HTMLLIElement).clientWidth;
    this.screenSize = [window.innerWidth, window.innerHeight];
    this.screenRatio = this.screenSize[1] / this.screenSize[0];

    document.addEventListener("keydown", this.onKeydown);
    document.addEventListener("keyup", this.onKeyup);

    // 초기 오브젝트 로드
    this.objects.forEach((item) => {
      item.element.style.position = "fixed";
      item.element.style.left = "0px";
      item.element.style.bottom = "0px";
      let itemF: HTMLImageElement | HTMLIFrameElement;
      if (item.type === "img") {
        itemF = document.createElement("img") as HTMLImageElement;
        itemF.src = item.contentF;
        itemF.style.width = "1px";
        itemF.style.height = "1px";
        itemF.style.backfaceVisibility = "hidden";
        item.innerElements.push(itemF);
        if (item.contentB !== "") {
          const itemImgB = document.createElement("img") as HTMLImageElement;
          itemImgB.src = item.contentB;
          itemImgB.style.width = "1px";
          itemImgB.style.height = "1px";
          itemImgB.style.backfaceVisibility = "hidden";
          item.element.appendChild(itemImgB);
          item.innerElements.push(itemImgB);
        } else {
          itemF!.style.backfaceVisibility = "visible";
          item.innerElements = [itemF!];
        }
      } else if (item.type === "iframe") {
        itemF = document.createElement("iframe") as HTMLIFrameElement;
        itemF.src = item.contentF;
        itemF.style.width = "375px";
        itemF.style.height = "812px";
        itemF.style.backfaceVisibility = "hidden";
        item.innerElements.push(itemF);
        const itemImgB = document.createElement("img") as HTMLImageElement;
        itemImgB.src = item.contentB;
        itemImgB.style.width = "375px";
        itemImgB.style.height = "812px";
        itemImgB.style.backfaceVisibility = "hidden";
        item.element.appendChild(itemImgB);
        item.innerElements.push(itemImgB);
      }
      item.element.appendChild(itemF!);
      this.$el.appendChild(item.element);

      // 미니맵
      let dot = document.createElement("div");
      dot.style.position = "absolute";
      dot.style.bottom = `${(item.state.y / this.map.yMax) * this.minimapSize}px`;
      dot.style.left = `${(item.state.x / this.map.xMax) * this.minimapSize}px`;
      dot.style.width = "2px";
      dot.style.height = "2px";
      dot.style.borderRadius = "50%";
      dot.style.backgroundColor = "blue";
      (this.$el.querySelector("#minimap") as HTMLLIElement).appendChild(dot);
    });
    this.$nextTick(() => {
      this.moveObjects();
    });
  }
  private beforeDestroy() {
    document.removeEventListener("keydown", this.onKeydown);
    document.removeEventListener("keyup", this.onKeyup);
  }
  private onKeydown(e: KeyboardEvent) {
    if (e.key === "ArrowUp") {
      this.keyIn.Au = true;
    } else if (e.key === "ArrowDown") {
      this.keyIn.Ad = true;
    } else if (e.key === "ArrowRight") {
      this.keyIn.Ar = true;
    } else if (e.key === "ArrowLeft") {
      this.keyIn.Al = true;
    } else if (e.key === "a") {
      this.keyIn.Dl = true;
    } else if (e.key === "d") {
      this.keyIn.Dr = true;
    } else if (e.key === "w") {
      this.keyIn.Du = true;
    } else if (e.key === "s") {
      this.keyIn.Dd = true;
    }
    if (!this.moving) {
      this.moving = true;
      requestAnimationFrame(this.move);
    }
  }
  private onKeyup(e: KeyboardEvent) {
    if (e.key === "ArrowUp") {
      this.keyIn.Au = false;
    } else if (e.key === "ArrowDown") {
      this.keyIn.Ad = false;
    } else if (e.key === "ArrowRight") {
      this.keyIn.Ar = false;
    } else if (e.key === "ArrowLeft") {
      this.keyIn.Al = false;
    } else if (e.key === "a") {
      this.keyIn.Dl = false;
    } else if (e.key === "d") {
      this.keyIn.Dr = false;
    } else if (e.key === "w") {
      this.keyIn.Du = false;
    } else if (e.key === "s") {
      this.keyIn.Dd = false;
    }
    if (!Object.values(this.keyIn).includes(true)) {
      this.moving = false;
    }
  }
  private move() {
    // 이동
    if (this.keyIn.Au && !this.keyIn.Ad) {
      this.state.y += Math.sin(this.state.thetaX) / 2;
      this.state.x += Math.cos(this.state.thetaX) / 2;
    } else if (!this.keyIn.Au && this.keyIn.Ad) {
      this.state.y -= Math.sin(this.state.thetaX) / 2;
      this.state.x -= Math.cos(this.state.thetaX) / 2;
    }
    if (this.keyIn.Ar && !this.keyIn.Al) {
      this.state.y += Math.sin(this.state.thetaX - Math.PI / 2) / 2;
      this.state.x += Math.cos(this.state.thetaX - Math.PI / 2) / 2;
    } else if (!this.keyIn.Ar && this.keyIn.Al) {
      this.state.y -= Math.sin(this.state.thetaX - Math.PI / 2) / 2;
      this.state.x -= Math.cos(this.state.thetaX - Math.PI / 2) / 2;
    }
    // 경계 설정
    if (this.state.y > this.map.yMax) {
      this.state.y = this.map.yMax;
    } else if (this.state.y < 0) {
      this.state.y = 0;
    }
    if (this.state.x > this.map.xMax) {
      this.state.x = this.map.xMax;
    } else if (this.state.x < 0) {
      this.state.x = 0;
    }
    // 방향 전환
    if (this.keyIn.Du && !this.keyIn.Dd) {
      this.state.thetaZ = this.state.thetaZ + 0.0115 > 0.25 ? this.state.thetaZ : this.state.thetaZ + 0.0115;
    } else if (!this.keyIn.Du && this.keyIn.Dd) {
      this.state.thetaZ = this.state.thetaZ - 0.0115 < -0.25 ? this.state.thetaZ : this.state.thetaZ - 0.0115;
    }
    if (this.keyIn.Dr && !this.keyIn.Dl) {
      this.state.thetaX -= 0.023;
    } else if (!this.keyIn.Dr && this.keyIn.Dl) {
      this.state.thetaX += 0.023;
    }
    // 경계 설정
    if (this.state.thetaX > 2 * Math.PI) {
      this.state.thetaX -= 2 * Math.PI;
    } else if (this.state.thetaX < 0) {
      this.state.thetaX += 2 * Math.PI;
    }
    if (this.state.thetaZ > 1) {
      this.state.thetaZ = 1;
    } else if (this.state.thetaZ < -1) {
      this.state.thetaZ = -1;
    }
    this.moveObjects();
    if (this.moving) {
      requestAnimationFrame(this.move);
    }
  }

  private moveObjects() {
    this.objects.forEach((item) => {
      // 상대 좌표, 각도 계산
      let relX = item.state.x - this.state.x;
      let relY = item.state.y - this.state.y;
      let relAngle = Math.atan2(item.state.y - this.state.y, item.state.x - this.state.x) - this.state.thetaX;
      // 계산하기 편하게 변환
      while (relAngle < -Math.PI || relAngle > Math.PI) {
        if (relAngle < -Math.PI) {
          relAngle += 2 * Math.PI;
        } else {
          relAngle -= 2 * Math.PI;
        }
      }
      // 특정 각도 미만에서 이미지 로드
      if (Math.abs(relAngle) < 1) {
        if (item.element.style.opacity === "0") {
          item.element.style.opacity = "1";
        }
        let r = Math.sqrt(relX * relX + relY * relY);
        let w = ((4 * Math.atan(item.state.width / 2 / r)) / Math.PI) * this.screenSize[0];
        let h = ((4 * Math.atan(item.state.height / 2 / r)) / Math.PI) * this.screenSize[0];

        let turnX = item.state.thetaX;
        let turnY = (item.state.thetaZ * Math.PI) / 180 - Math.atan2(-relY, -relX) - Math.PI;
        item.element.style.transform = `translate(${this.screenSize[0] / 2 - relAngle * 2 * this.screenSize[0]}px, ${-(this.screenSize[1] / 2 - this.state.thetaZ * 2 * this.screenSize[1])}px)`;
        if (item.type === "img") {
          item.innerElements[0].style.transform = `scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${turnY}rad) rotateX(${turnX}deg)`;
        } else {
          item.innerElements[0].style.transform = `scaleX(${w / 375}) scaleY(${h / 812}) perspective(${r}px) rotateY(${turnY}rad) rotateX(${turnX}deg)`;
        }
        if (item.innerElements.length === 2) {
          item.innerElements[1].style.transform = `scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${turnY + Math.PI}rad) rotateX(${-turnX}deg)`;
        }
        item.element.style.zIndex = `${this.map.xMax + this.map.yMax - Math.floor(r)}`;
      } else {
        if (item.element.style.opacity !== "0") {
          item.element.style.opacity = "0";
        }
      }
    });
  }

  private meTransform() {
    return `translate(${(this.state.x / this.map.xMax) * this.minimapSize}px,-${(this.state.y / this.map.yMax) * this.minimapSize}px)  rotate(${45 - (this.state.thetaX * 180) / Math.PI}deg)`;
  }
}
